<template lang="jade">
.security
 ul.secNav
  li.secNavs(v-for='(nav,$index) in $store.state.center.secNavs')
   p
    icon(:name='nav.name')
   p {{nav.text}}
   v-touch(tag='p',@tap="bindShow($index)",v-show='nav.toggle') {{nav.bind}}
   p {{nav.tip}}
 layer(v-show="$store.state.layer.cen_layer")
</template>
<script>
import layer from "./layer/cen_layer.vue"
	export default{
		methods:{
			bindShow(value){
				this.$store.commit('bind',value);
			},
		},
		components:{
			layer
		}
	}
</script>
<style lang="scss">
     @import "../../assets/css/main.scss";
     .security{
     	width:rem2px(750);
      @extend %m0a;
     }
    .secNav{
    	width:rem2px(750);
    	//height:rem2px(6000);
    	background-color:#fff;
    	padding-top:0.4rem;
    	@extend %bz;
    	border-bottom:2px solid #d5d5d5;
    	color:#737373;
    	position:absolute;
    }
	.secNavs{
		@extend %m0a;
    @extend %bz;
		width:rem2px(680);
		height:rem2px(110);
    padding-top:rem2px(20);
		line-height: 1.4843rem;
		border-bottom:1px solid #d5d5d5;
		vertical-align: middle;
    @extend %font24;
		>p{
      @extend %fl;
		}
		p:nth-child(1){
		  width:0.65rem;
      height:0.65rem;
      border-radius:100%;
      position:relative;
      top:0.36rem;
      margin-left:rem2px(10);
		}
		p:nth-child(2){
      margin-left:rem2px(30);
		}
		p:nth-child(4){
         color:#d5d5d5;
         @extend %fr;
         text-align: center;
         width:7rem;
         @extend %font24;
         @extend %abso;
         left:6rem;
		}
		p:nth-child(3){
			width:1.5rem;
			height:0.7rem;
			border:1px solid #d5d5d5;
			border-radius:0.12rem;
			position:relative;
			top:0.38rem;
			line-height:0.7rem;
            @extend %fr;
      margin-right:rem2px(20);
		}
		svg{
			width:0.4375rem;
			height:0.35rem;
			color:#fff;
			position:relative;
			top:-0.36rem;
		}
	}
	.secNav{
		li:nth-child(1){
			p:nth-child(1){
				background-color:#b0b3fe;
			}
		}
		li:nth-child(2){
			p:nth-child(1){
				background-color:#60d3ff;
			}
			svg{
				width:0.4375rem;
				height:0.46rem;
				top:-0.28rem;
			}
		}
		li:nth-child(3){
			p:nth-child(1){
				background-color:#e4adff;
			}
		}

		li:nth-child(4){
			p:nth-child(1){
				background-color:#ecb4b4;
			}
		}

		li:nth-child(5){
			p:nth-child(1){
				background-color:#fb9696;
			}
		}
		li:nth-child(6){
			p:nth-child(1){
				background-color:#56c560;
			}
		}
	}
</style>



