<template lang="jade">
.index
 bSlide
 .marqu
  .marr
   marquee(scrollamount=4)
    span(v-for='item in $store.state.getNotice')
     {{item.text}}
 .ishow
  .navIndex
   .top
    v-touch(tag='div',@tap="swiperTo(1,1)")
    v-touch(tag='div',@tap="swiperTo(2)")
    v-touch(tag='div')
   .bottom
    v-touch(tag='div',@tap="swiperTo(1,0)")
    v-touch(tag='div')
</template>
<script>
  import bSlide from './banner/bannerSlide.vue';
	export default{
       components:{
       	 bSlide
       },
       methods:{
          swiperTo(mainValue,gameValue){
            if(mainValue==1){
              this.$store.commit("showToCenter",{main:mainValue,game:gameValue});
            }else{
              this.$store.commit("swiperTopage",mainValue);
            }
          },
          getNotice(){
            this.axios({
              method:'get',
              url:this.$store.state.url+'dist/php/ac.php?action=getnotice',
              headers:this.$store.state.register.headers,
              params:{
                note_type:1
              }
            }).then((response)=>{
              response.data.info.forEach((item)=>{
                (this.$store.state.getNotice).push({text:item.note_content});
              })
            })
          }
       },
       mounted:function(){
          setTimeout(()=>{this.getNotice();},200)
       }
	}
</script>
<style lang="scss">
  @import "../assets/css/main.scss";
  .tipHead_1{
    @extend %m0a;
    @include Bg('showTip1.png',2.85rem,0.296875rem);
    background-size:2.85rem,0.296875rem;
  }
  .tipHead{
    @extend %m0a;
    @include Bg('showTip2.png',3.5rem,0.3125rem);
    background-size:3.5rem 0.3125rem;
  }
  .logo{
  	 @extend %fl;
     @include Bg("logo.png",rem2px(105),rem2px(36))
     margin-left:rem2px(30);
     @extend %bsc;
  }
  .marqu{
  	 @include Bg('marBg.jpg',100%,rem2px(47));
     @extend %m0a;
     display:flex;
     align-items:center;
     justify-content:flex-end;
     width:rem2px(750);
     height:rem2px(47);
  	 background-size:100% ;
  	 box-sizing:border-box;
  	 color:#fff;
  	 @extend %font16;
  	 .marr{
       width:75%;
       @extend %fl;
       display:flex;
       align-items:center;
	    >marquee{
	      color:#fff;
	    	width:rem2px(500);
        height:rem2px(40);
        line-height:rem2px(40);
        vertical-align:middle;
        span{
          margin-right:rem2px(200);
        };
	    }
  	 }
  }
  .ishow{
    @include Bg('index_bg.jpg',rem2px(750),rem2px(760));
  	box-sizing:border-box;
    background-size:rem2px(750) rem2px(760);
  }
  .navIndex{
    .top{
      display:flex;
      justify-content:center;
      padding-top:rem2px(110);
      >div:nth-child(2){
         margin:0 rem2px(90);
      }
      @for $i from 1 through 3{
        >div:nth-child(#{$i}){
          @include Bg('nav_t'+$i+".png",rem2px(148),rem2px(138));
          @extend %bsc;
        }
      }
    }
    .bottom{
      display:flex;
      justify-content:center;
      margin-top:rem2px(5);
        div:last-child{
          margin-left:rem2px(100);
        }
        @for $i from 1 through 2{
          >div:nth-child(#{$i}){
            @include Bg('nav_b'+$i+'.png',rem2px(148),rem2px(138));
            @extend %bsc;
          }
        }
    }
  }
</style>



