<template lang="jade">
.centerModules(ref="center")
 .mtitle#mtitle
   v-touch(@tap="returnCenter",class="backend")
    icon(name="angle-left")
    | 返回
   {{this.$store.state.centerTitle}}
 transform(v-if="$store.state.centers[0].toggle")
 security(v-if="$store.state.centers[1].toggle")
 message(v-if="$store.state.centers[2].toggle")
 recharge(v-if="$store.state.centers[3].toggle")
 withdraw(v-if="$store.state.centers[4].toggle")
 record(v-if="$store.state.centers[5].toggle")
</template>
<script>
    import transform from "./center/center_tran.vue";  //转账页面
    import security from "./center/center_sec.vue";   //安全设置页面
    import message from "./center/center_mess.vue";//message页面
    import recharge from "./center/center_recha.vue";//recharge页面
    import withdraw from './center/center_wit.vue';//提现页面
    import record from './center/center_reco.vue' //记录
	export default{
		methods:{
			returnCenter(){
        this.$store.commit("returnCenter")
			}
		},
		components:{
			transform,
			security,
			message,
			recharge,
			withdraw,
			record
		}
	}
</script>



